.spc-controls-content .spc-full-range-wrapper>.spc-range-control-wrapper,
.spc-controls-content .spc-full-range-wrapper>.spc-range-control-wrapper>div {
    margin-bottom: 0;
}

.spc-controls-content .spc-full-range-wrapper>.spc-range-control-wrapper>div {
    display: flex;
    margin-right: 2px;
}

/* Measurements */
.spc-title-bar {
    margin-bottom: 8px;
    display: flex;
    align-items: center;

    .spc-control-title {
        flex-grow: 1;
    }

    button.components-button.has-icon.is-reset {
        width: 20px;
        min-width: 20px;
        padding: 0 3px;
        margin: 0 5px 0 0;
    }
}

.spc-range-control-range .components-range-control__root svg {
    margin-top: 2px;
}

.spc-controls-content .measure-input-wrap .spc-input-setting-toggle {
    height: 14px;

    .components-button.spc-input-setting-toggle-btn {
        width: 100%;
        height: 14px;
        padding: 0;
        line-height: 12px;
        font-size: 7px !important;
        margin: 0;
        display: block;
        border: 1px solid #e2e4e7;
        border-radius: 0;
        text-transform: uppercase;
        background-color: #fff;
    }
}

.spc-measure-control .spc-controls-content {
    align-items: flex-start;
}

.components-popover.spc-range-popover-settings .components-popover__content>div {
    padding: 30px 20px;
    min-width: 280px;
}

.components-button-group.spc-preset-size-options {
    display: flex;
    flex-flow: wrap;
    margin: -1%;
}

.components-button-group.spc-preset-size-options button.components-button.spc-preset-sizes {
    flex-basis: 18%;
    width: auto;
    min-width: auto;
    margin: 1%;
    border-radius: 0;
    text-align: center;
    padding: 0 2px;
    justify-content: center;
}

.components-button-group.spc-measure-responsive-options {
    .components-button.spc-responsive-btn {
        background: transparent;
        height: auto;
        border: 0;
        box-shadow: none;
        padding: 2px 4px;
        margin-bottom: 0;
        outline: 0;
        color: #A0AEC0;
        width: auto;
        font-size: 10px;

        &:hover {
            svg {
                fill: #007cba !important;

                path {
                    fill: #007cba;
                }

                rect {
                    stroke: #007cba;
                }
            }

            color: #007cba;
        }

        &.is-active {
            svg {
                fill: #007cba;

                path {
                    fill: #007cba;
                }

                rect {
                    stroke: #007cba;
                }
            }

            color: #007cba;
            background: transparent;
        }

        .dashicon {
            width: 15px;
            height: 15px;
            font-size: 15px;
        }
    }
}

.spc-controls-content {
    display: flex;

    .spc-range-control {
        flex-grow: 1;
        margin-right: 2px;
        margin-bottom: 0;

        .components-base-control__field {
            margin-bottom: 0;
            display: flex;
        }
    }

    .measure-input-wrap {
        // margin-right: 2px;
        flex-grow: 1;

        .components-base-control.measure-inputs {
            margin: 0;
            position: relative;

            .components-base-control__field {
                margin-bottom: 0;
            }

            input[type=number] {
                padding: 0 5px;
                margin: 0;
                -moz-appearance: textfield;
                border: 1px solid #000000;
                border-radius: 0;
                box-shadow: inset 0px 2px 3px rgba(0, 0, 0, 0.1);

                &::-webkit-outer-spin-button,
                &::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }

                &[disabled] {
                    background: #e4e7ea;
                }
            }
        }
    }

    .measure-label {
        display: none;
        font-size: 11px;
        line-height: 16px;
        padding: 3px 0;
        text-align: center;

        svg {
            height: 16px;
            width: 16px;
        }
    }
}

.spc-measure-control {

    .measure-input-top .measure-inputs .components-base-control__field>.components-text-control__input {
        border-right: 0 !important;
        border-radius: 2px 0 0 2px !important;
    }

    .measure-input-right .measure-inputs .components-base-control__field>.components-text-control__input {
        border-right: 0 !important;
    }

    .measure-input-bottom .measure-inputs .components-base-control__field>.components-text-control__input {
        border-right: 0 !important;
    }

    .measure-input-left .measure-inputs .components-base-control__field>.components-text-control__input {
        border-right: 0 !important;
    }

    &.measure-mobile-size .measure-input-left .measure-inputs .components-base-control__field>.components-text-control__input,
    &.measure-tablet-size .measure-input-left .measure-inputs .components-base-control__field>.components-text-control__input {
        border-right: 1px solid #000000 !important;
    }
}

