.spc-units {
    max-width: 60px;
    display: flex;

    .components-button {
        padding-top: 2px;
        padding-bottom: 2px;

        svg {
            width: 16px;
        }
    }
}

.spc-units button.components-button.is-active.is-single.is-small {
    min-width: 20px;
    height: 30px;
    border: 1px solid #e2e4e7;
    background-color: #fff;
    display: flex;
    margin-right: 2px;
}

.spc-units .spc-units-group .components-button {
    min-width: 20px;
    height: 30px;
    border: 1px solid #000000;
    background-color: #fff;
    border-radius: 0 2px 2px 0 !important;
    display: flex;
    margin-right: 2px;
}

.spc-units .components-dropdown.components-dropdown-menu.components-toolbar {
    background-color: transparent;
    display: flex;
    height: 30px;
    min-height: 30px;
    border: 1px solid #e2e4e7;

    .components-button {
        height: 30px;
        min-width: 10px;
        padding-top: 2px;
        padding-left: 4px;
        padding-right: 4px;
        padding-bottom: 2px;
        margin-left: 2px;

        &:before {
            display: none;
        }
    }
}

.spc-locked>button {
    display: flex;
    justify-content: center !important;
}

.spc-locked>button>svg {
    margin-right: 0 !important;
}

.spc-locked .components-button.is-single {
    display: flex;
    height: 30px;
}

.spc-locked>.spc-unit-btn-linked {
    background-color: #327BB5;
    border: 1px solid #103757;
}

.spc-locked>.spc-unit-btn-linked>svg>path {
    fill: #ffffff;
    stroke: #ffffff;
}

.spc-locked>.spc-unit-btn-not-linked {
    background-color: #EEF6FD;
    border: 1px solid #9BBDD8;
}

.spc-locked>.spc-unit-btn-not-linked>svg>path {
    fill: #327BB5;
}

.spc-locked>.spc-unit-btn-linked:focus,
.spc-locked>.spc-unit-btn-not-linked:focus {
    outline: 0;
    box-shadow: none;
}

.components-dropdown__content.spc-units-popover .components-popover__content {
    min-width: 48px;
    width: 48px;

    .components-button.components-dropdown-menu__menu-item {
        min-width: 30px;
    }

    >div {
        padding: 5px
    }
}