.spc-typography-range-options {
    width: 100%;
    position: relative;
    margin: 10px 0 0 0;
}

.spc-typography-font-family-wrapper {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.spc-typography-font-family-wrapper .react-select__control {
    width: 126px;
}

.spc-typography-range-options .components-base-control__label {
    width: auto;
    display: block;
    margin-bottom: 8px;
}

.spc-typography {
    width: 100%;
}

.spc-responsive-control-options {
    position: absolute;
    margin-top: 0;
    margin-left: 0;
    top: 0;
    right: 0;
    z-index: 10;
}

.spc-typography-reset-btn {
    color: #327BB5;
    padding: 11px;
    font-size: 13px !important;
    border: 1px solid #9BBDD8;
    background: #EEF6FD;
    border-radius: 3px;
    height: 34px !important;
    box-shadow: none;
    line-height: 1;
    margin-left: 10px;
}

.spc-responsive-control-options .spc-responsive-tabs {
    height: 15px;
    padding: 5px 5px !important;
    box-shadow: none !important;
    border: none !important;
}

.spc-responsive-control-options .spc-responsive-tabs.active-tab>span {
    color: #007cba;
    border: none !important;
}

.spc-responsive-control-options .spc-responsive-tabs>span {
    height: 15px;
    width: 15px;
    font-size: 15px;
}

.spc-relative-row {
    position: relative;
}

.spc-typography-option-actions {
    display: flex;
    align-items: center;

    .spc-typography-text {
        margin-right: 90px;
    }
}

.spc-typography-options {
    position: relative;

    .spc-typography-advanced {
        position: absolute;
        width: 100%;
        top: 105%;
        z-index: 11;
        left: 0;
        background: #ffffff;
        border: 1px solid #e5e5e5;
        padding: 10px;
    }
}

.spc-typography-font-family-options {
    position: relative;
    z-index: 11;
}

.spc-custom-row {
    margin-top: 0;
}

.react-select-container input:focus {
    box-shadow: none !important;
}

.spc-typography-control-btn {
    position: relative;
    display: block;
    height: 32px !important;
    width: 32px !important;
    background: #327BB5 !important;
    border: 1px solid #103757 !important;
    border-radius: 2px;
}

.spc-typography-control-btn svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.spc-responsive-tabs>svg {
    fill: none;
}

.spc-typography-control-btn>svg>path {
    fill: #ffffff;
    stroke: #ffffff;
}

.spc-responsive-tabs.active-tab>svg>path,
.spc-responsive-tabs:hover>svg>path {
    fill: #327BB5;
}

.spc-responsive-tabs.active-tab>svg>rect,
.spc-responsive-tabs:hover>svg>rect {
    stroke: #327BB5;
}

.spc-size-type-field-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.components-spc-control-buttons__units {
    margin-left: 20px;
    position: absolute;
    top: -4px;

    .spc-size-type-field {
        button.spc-size-btn {
            background: #fff;
            box-shadow: none !important;
            color: #000000;
            font-size: 13px;
            padding: 0 5px;
            position: relative;
            text-align: center;
            text-shadow: none;
            border: 0;
            border-radius: 0 !important;
            line-height: 20px;
            padding: 0 5px;
            height: auto;

            &[aria-pressed="true"] {
                background: #fff !important;
                color: #327BB5 !important;
                cursor: default;
                z-index: 1;
            }
        }
    }
}

.spc-select-control-wrapper {
    align-items: center !important;
    flex-direction: row !important;
    gap: 25px;

    >.components-input-control__container {
        max-width: 126px;

        >.components-select-control__input {
            height: 40px !important;
            line-height: 1.3 !important;
        }

        >.components-input-control__backdrop {
            border: 1px solid #E0E0E0 !important;
            box-shadow: inset 0px 2px 3px rgba(0, 0, 0, 0.1);
            border-radius: 2px;
        }
    }
}