.spc-full-range-wrapper {
    position: relative;
    width: 100%;
}

.spc-full-range-wrapper>.spc-size-type-field {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
}

.spc-full-range-wrapper>.spc-size-type-field {
    >.spc-size-btn {
        box-shadow: none;
        padding: 0;
        line-height: 1;
        height: auto;
        font-size: 14px;

        &:not(:last-child) {
            margin-right: 10px;
        }

        &[aria-pressed="true"] {
            box-shadow: none;
            background: transparent;
            color: #327BB5
        }
    }
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider+span {
    height: 6px;
    border: 1px solid #000000;
    background-color: transparent;
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track {
    height: 6px;
    border: 1px solid #000000;
    background-color: #000000;
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track+span,
.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track+span>span {
    background: #327BB5;
    border: 1px solid #FFFFFF;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 100px;
    width: 22px;
    height: 22px;
    overflow: hidden;
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track+span>span:before {
    position: absolute;
    content: '';
    background-image: url("data:image/svg+xml,%3Csvg width='7' height='8' viewBox='0 0 7 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fillRule='evenodd' clipRule='evenodd' d='M0 0.5C0 0.223858 0.223858 0 0.5 0C0.776142 0 1 0.223858 1 0.5V7.5C1 7.77614 0.776142 8 0.5 8C0.223858 8 0 7.77614 0 7.5V0.5ZM3 0.5C3 0.223858 3.22386 0 3.5 0C3.77614 0 4 0.223858 4 0.5V7.5C4 7.77614 3.77614 8 3.5 8C3.22386 8 3 7.77614 3 7.5V0.5ZM6.5 0C6.22386 0 6 0.223858 6 0.5V7.5C6 7.77614 6.22386 8 6.5 8C6.77614 8 7 7.77614 7 7.5V0.5C7 0.223858 6.77614 0 6.5 0Z' fill='white'/%3E%3C/svg%3E%0A");
    height: 8px;
    width: 7px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track+span {
    margin-top: 4px;
    z-index: 11;
}

.spc-range-control-wrapper .components-range-control__wrapper>.components-range-control__slider~.components-range-control__track+span>span>span {
    display: none;
}

.spc-range-control-wrapper .components-input-control__backdrop {
    border: 1px solid #000;
    border-radius: 3px;
    box-shadow: inset 0 2px 3px 0px rgba(0, 0, 0, 0.1);
}

.spc-range-control-wrapper .components-range-control__reset {
    color: #327BB5;
    padding: 11px;
    font-size: 13px;
    border: 1px solid #9BBDD8;
    background: #EEF6FD;
    border-radius: 3px;
    height: unset;
    box-shadow: none;
    line-height: 1;
    transition: all .3s ease;
}

.spc-range-control-wrapper .components-range-control__reset:hover {
    border: 1px solid #9BBDD8;
    box-shadow: none !important;
}