.wp-block-spc-woo-product {
	position: relative;
}

.spc-woo-product-wrapper {

	position: relative;

	.swiper-container {
		margin-left: auto;
		margin-right: auto;
		position: relative;
		overflow: hidden;
		z-index: 1;
	}

	.swiper-container-autoheight .swiper-wrapper {
		-webkit-box-align: start;
		-ms-flex-align: start;
		align-items: flex-start;
		-webkit-transition-property: height;
		transition-property: height;
		-o-transition-property: transform, height;
		transition-property: transform, height;
		transition-property: transform, height;
	}

	.js-spc-swiper-pagination {
		margin-top: 50px;
		position: relative;
	}

	.is-grid.spc-woo-product-items {
		display: flex;
		-ms-flex-wrap: wrap;
		-webkit-flex-wrap: wrap;
		flex-wrap: wrap;
		margin-right: -10px;
		margin-left: -10px;

		article {
			padding-right: 10px;
			padding-left: 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
		}


	}

	.spc-woo-product-columns-8 article {
		width: 12.5%;
	}

	.spc-woo-product-columns-7 article {
		width: 14.28%;
	}

	.spc-woo-product-columns-6 article {
		width: 16.66%;
	}

	.spc-woo-product-columns-5 article {
		width: 20%;
	}

	.spc-woo-product-columns-4 article {
		width: 25%;
	}

	.spc-woo-product-columns-3 article {
		width: 33.2%;
	}

	.spc-woo-product-columns-2 article {
		width: 50%;
	}

	.spc-woo-product-columns-1 article {
		width: 100%;
	}

	div[class*="columns"].is-grid {
		@media only screen and (max-width: 600px) {
			grid-template-columns: 1fr;
		}
	}

	&.spc-editor-preview-mode-tablet {
		.spc-woo-product-columns-tablet-1 article {
			width: 100%;
		}

		.spc-woo-product-columns-tablet-2 article {
			width: 50%;
		}

		.spc-woo-product-columns-tablet-3 article {
			width: 33.2%;
		}

		.spc-woo-product-columns-tablet-4 article {
			width: 25%;
		}

		.spc-woo-product-columns-tablet-5 article {
			width: 20%;
		}

		.spc-woo-product-columns-tablet-6 article {
			width: 16.66%;
		}

		.spc-woo-product-columns-tablet-7 article {
			width: 14.28%;
		}

		.spc-woo-product-columns-tablet-8 article {
			width: 12.5%;
		}
	}

	&.spc-editor-preview-mode-mobile {
		.spc-woo-product-columns-mobile-1 article {
			width: 100%;
		}

		.spc-woo-product-columns-mobile-2 article {
			width: 50%;
		}

		.spc-woo-product-columns-mobile-3 article {
			width: 33.2%;
		}

		.spc-woo-product-columns-mobile-4 article {
			width: 25%;
		}

		.spc-woo-product-columns-mobile-5 article {
			width: 20%;
		}

		.spc-woo-product-columns-mobile-6 article {
			width: 16.66%;
		}

		.spc-woo-product-columns-tablet-7 article {
			width: 14.28%;
		}

		.spc-woo-product-columns-tablet-8 article {
			width: 12.5%;
		}
	}

	@media (max-width: 976px) {

		.spc-woo-product-columns-tablet-1 article {
			width: 100%;
		}

		.spc-woo-product-columns-tablet-2 article {
			width: 50%;
		}

		.spc-woo-product-columns-tablet-3 article {
			width: 33.2%;
		}

		.spc-woo-product-columns-tablet-4 article {
			width: 25%;
		}

		.spc-woo-product-columns-tablet-5 article {
			width: 20%;
		}

		.spc-woo-product-columns-tablet-6 article {
			width: 16.66%;
		}

		.spc-woo-product-columns-tablet-7 article {
			width: 14.28%;
		}

		.spc-woo-product-columns-tablet-8 article {
			width: 12.5%;
		}
	}

	@media (max-width: 767px) {
		.spc-woo-product-columns-mobile-1 article {
			width: 100%;
		}

		.spc-woo-product-columns-mobile-2 article {
			width: 50%;
		}

		.spc-woo-product-columns-mobile-3 article {
			width: 33.2%;
		}

		.spc-woo-product-columns-mobile-4 article {
			width: 25%;
		}

		.spc-woo-product-columns-mobile-5 article {
			width: 20%;
		}

		.spc-woo-product-columns-mobile-6 article {
			width: 16.66%;
		}

		.spc-woo-product-columns-tablet-7 article {
			width: 14.28%;
		}

		.spc-woo-product-columns-tablet-8 article {
			width: 12.5%;
		}
	}


	.spc-product-image {

		img {
			display: block;
			width: 100%;
		}
	}

	a,
	a:hover {
		text-decoration: none !important;
	}

	.spc-woo-product-load-btn {
		.spc-woo-product-loadmore-btn {
			color: #fff;
			font-weight: 300;
			font-size: 16px;
			line-height: 1em;
			margin: 20px 0;
			background-color: #7a56ff;
			padding: 8px 15px;
			border-width: 1px;
			border-style: solid;
			border-color: #7a56ff;
			border-radius: 0;
			box-shadow: 0 0 0 0 #000;
		}
	}

	//swiper navigation

	.spc-carousel-nav-next,
	.spc-carousel-nav-prev {
		position: absolute;
		z-index: 2;
		width: 60px;
		height: 60px;
		background: #fff;
		text-align: center;
		border-radius: 50%;
		border: 1px solid #e5e5e5;
		opacity: 1;
		transition: .3s ease;
		cursor: pointer;
		top: 50%;
		transform: translateY(-50%);
	}

	.spc-carousel-nav-next:hover,
	.spc-carousel-nav-prev:hover {
		color: #fff;
		box-shadow: 0 19px 27px 0 rgba(0, 0, 0, 0.1);
		background: #806bff;
	}

	.spc-carousel-nav-prev {
		left: 56px;
	}

	.spc-carousel-nav-next {
		left: 126px;
	}

	.spc-carousel-nav-next {
		width: 50px;
		height: 50px;
		left: auto;
		right: 0px;
		top: 50%;
		border-radius: 50px 50px 50px 50px;
		background: #806bff;
	}

	.spc-carousel-nav-prev {
		width: 50px;
		height: 50px;
		left: 0px;
		top: 50%;
		border-radius: 50px 50px 50px 50px;
		background: #806bff;
	}

	@media(min-width: 1024px) {
		.spc-carousel-nav-prev {
			left: -60px;
		}

		.spc-carousel-nav-next {
			right: -60px;
		}
	}

	.spc-carousel-nav-next svg,
	.spc-carousel-nav-prev svg {
		top: 50%;
		position: absolute;
		left: 50%;
		transform: translate(-50%, -50%);
		font-size: 20px;
		transition: .3s ease;
		color: #b1b1b1;
		line-height: 58px;
	}

	//dots style 
	.spc-dots-container {
		position: relative;
		display: block;
		margin-top: 50px;

	}

	.spc-swiper-pagination .swiper-pagination-bullet {
		border-radius: 0 0 0 0;
		height: 10px;
		width: 10px;
		background: #e5e5e5;
		opacity: 1;
	}

	.spc-swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
		background: #000;
	}

	.spc-swiper-pagination .swiper-pagination-bullet:not(:last-child) {
		margin-right: 8px;
	}

	.swiper-pagination.swiper-pagination-progressbar {
		position: relative;
	}
}

.is-root-container.block-editor-block-list__layout button.components-button.spc-prebuilt {
	background: var(--wp-admin-theme-color, #00669b);
	background: var(--wp-admin-theme-color);
	color: white;
	padding: 0;
	margin: 0;
	line-height: 45px;
	font-size: 14px;
	font-weight: bold;
	margin-top: 10px;
	margin-right: 0px;
	margin-bottom: 10px;
	height: 45px;
	padding: 0 10px;
	text-align: center;
	border: 0;
	border-radius: 2px;
	box-shadow: none;
}

.is-root-container.block-editor-block-list__layout button.components-button.spc-prebuilt:hover,
.is-root-container.block-editor-block-list__layout button.components-button.spc-prebuilt:focus:enabled {
	background: #006ba1;
	background: var(--wp-admin-theme-color-darker-10);
	color: white;
	box-shadow: none;
}

.spc-inspect-tabs .components-tab-panel__tabs {
	width: 100%;
	justify-content: space-between;
	margin-bottom: 10px;
	border-bottom: none;

	.spc-tabs-control {
		border-bottom: 2px solid #E0E0E0;
		width: 100%;
		justify-content: center;
		font-size: 14px;

		&.active-tab {
			border-bottom: 2px solid #327BB5;
			color: #327BB5;
			border-top: none;
		}

		&:hover {
			border-bottom: 2px solid #327BB5;
			color: #327BB5;
		}

		&:focus {
			box-shadow: none;
		}
	}
}