.spc-sort-control {
	.spc-sort-control__container {
		display: flex;
		flex-direction: row;
	}

	&.spc-sort-control--axis-y {
		text-align: center;

		.spc-sort-control__container {
			flex-direction: column;
			max-width: 150px;
			margin: 0 auto;
		}

		.spc-control__reset-button.is-tertiary {
			margin-top: 10px;
			color: #327BB5;
			padding: 11px;
			font-size: 13px;
			border: 1px solid #9BBDD8;
			background: #EEF6FD;
			border-radius: 3px;
			height: unset;
			box-shadow: none;
			line-height: 1;
			transition: all 0.3s ease;
			margin-left: 8px;
			height: 30px;
			min-height: 30px;

		}
	}

	.spc-sort-control__item {
		box-shadow: none;
	}
}

.spc-sort-control__item {
	color: #327BB5;
	border-radius: 4px;
	background: #fff;
	width: 100%;
	text-align: center;
	padding: 4px;
	border: 1px solid #9BBDD8;
	margin: 2px;
	display: block;
	cursor: grab !important;
	box-shadow: inset 0 2px 3px 0px rgb(0 0 0 / 10%);

	&:hover {
		background: #EEF6FD;
		box-shadow: inset 0 0 0 1px #9BBDD8;
	}
}