.spc-color-component-settings {
    position: relative;
    margin-top: 15px;
    margin-bottom: 15px;

    &>.spc-color-component-custom-text {
        margin: 0;
        position: absolute;
        top: 5px;
        font-size: 15px;
    }

    &>.spc-color-component-custom-class {
        &>.components-dropdown {
            height: 32px;
            width: 110px;
            border: 1px solid #000000;
            border-radius: 4px;
            overflow: hidden;
            margin-left: auto;
            box-shadow: inset 0 2px 3px 0px rgba(0, 0, 0, 0.1);

            &>.components-color-palette__custom-color {
                height: 32px;
                width: 32px;
                position: relative;
                margin-left: auto;
                border-radius: 0 2px 2px 0;
                border-width: 0 0 0 1px;
                border-style: solid;
                border-color: #000000;

                &[aria-label="Custom color picker."] {
                    border-width: 0 0 0 1px;
                    border-style: solid;
                    border-color: #e0e0e0;
                    box-shadow: none;
                    background-position: center center;
                    background-image: url("data:image/svg+xml,%3Csvg width='31' height='32' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23FF2121' d='m30.251.655.707.707-30.25 30.25L0 30.907z'/%3E%3C/svg%3E");
                }

                &>.components-color-palette__custom-color-name {
                    display: none;
                }

                &>.components-color-palette__custom-color-value {
                    position: absolute;
                    right: 200%;
                    top: 50%;
                    transform: translateY(-56%);
                    color: #000000;
                    white-space: nowrap;
                }
            }
        }

        &>.components-circular-option-picker {
            &>.components-circular-option-picker__custom-clear-wrapper {
                &>.components-circular-option-picker__clear {
                    color: #327BB5;
                    padding: 11px;
                    font-size: 13px;
                    border: 1px solid #9BBDD8;
                    background: #EEF6FD;
                    border-radius: 3px;
                    height: unset;
                    box-shadow: none;
                    line-height: 1;
                }
            }
        }
    }
}

// .spc-color-component-custom-class .components-color-palette__custom-color[aria-label="Custom color picker."] ~ .components-dropdown {
// }

.spc-color-component-settings>.spc-color-component-custom-class>.components-dropdown:has(.components-color-palette__custom-color[aria-label="Custom color picker."]) {
    border: 1px solid #E0E0E0;
}