.spc-woo-product-inner-wrap {}

/* Quick view button */
.quickview-wrapper {
    display: inline-block;
    text-align: center;

    .spc-product-quickview-btn {
        background: rgba(255, 255, 255, 0.2);
        padding: 10px 10px;
        width: 100%;
        color: #fff;
        display: inline-block;
        box-sizing: border-box;
        line-height: 1;

        .dashicons {
            margin-right: 5px;
        }
    }


}

/* Quick view */
#spc-viewmodal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    background-color: transparent;
    overflow-y: auto;
}

#spc-viewmodal.spcquickview-open {
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.75);
}

#spc-viewmodal .spc-close-btn {
    background: transparent none repeat scroll 0 0;
    color: #000;
    height: inherit;
    line-height: inherit;
    padding: 0;
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
    display: none;
    text-align: center;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: none;
    border-radius: 50%;
}

#spc-viewmodal .spc-close-btn:hover {
    color: red;
    background: transparent;
}

#spc-viewmodal .spc-modal-quickview-dialog {
    max-width: 960px;
    padding: 35px;
    width: 100%;
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
    border-radius: 10px;
}

#spc-viewmodal.spcquickview-open .spc-modal-quickview-dialog {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s ease 0.3s
}

#spc-viewmodal.woocommerce div.product .product_title {
    color: #010101;
    font-size: 24px;
    line-height: 1;
    margin: 15px 0;
    display: block;
}

.content-spcquickview.entry-summary {
    margin: 0;
    max-height: auto;
    overflow-y: auto;
}

#spc-viewmodal.woocommerce div.product p.price,
#spc-viewmodal.woocommerce div.product span.price {
    color: #444444;
    font-size: 1.25em;
    margin-bottom: 15px;
}

#spc-viewmodal.woocommerce div.product .woocommerce-product-rating {
    margin: 15px 0;
}

#spc-viewmodal.woocommerce button.button.alt {
    background-color: #444444;
    color: #fff;
    font-weight: normal;
    line-height: 1;

}

#spc-viewmodal.woocommerce .ht-qwick-view-left .images {
    float: left !important;
}

.ht-quick-view-thumbnails .ht-quick-thumb-single {
    cursor: pointer;
    padding: 0 2px;
}

.ht-quick-view-thumbnails .slick-slide img {
    width: 100%;
    border: 1px solid #cccccc;
}

#spc-viewmodal .quantity {
    float: left;
    margin-right: 10px;
    width: auto;
}

#spc-viewmodal.woocommerce .quantity .qty {
    text-align: center;
    width: 3.631em;
}

#spc-viewmodal.woocommerce .product_meta {
    margin-top: 20px;
}

#spc-viewmodal .star-rating {
    float: left;
    margin-right: 10px;
    margin-top: 4px;
}

#spc-viewmodal.woocommerce div.product div.images {
    margin-bottom: 5px;
}

#spc-viewmodal.woocommerce div.product .woocommerce-product-gallery__image img {
    border: 1px solid #ccc;
}

@media (max-width: 450px) {
    #spc-viewmodal .spc-row-wrapper {
        flex-direction: column;
    }

    #spc-viewmodal .spc-row-wrapper .spc-col.spc-col-2 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

.spc-row-wrapper .spc-col.spc-col-2 {
    flex: 0 0 50%;
    max-width: 50%;
}

.spc-quick-view-img-wrapper .woocommerce-product-gallery {
    opacity: 1;
}

.spc-quick-view-img-wrapper .spc-quick-view-thumbnails {

    width: 100px;
    float: left;
}

#spc-viewmodal .woocommerce .spc-product-image div.images .flex-control-thumbs li img {
    border: 5px solid #fff;
}

#spc-viewmodal .woocommerce div.product form.cart {
    overflow: hidden;
}

.spc-modal-quickview-body button.button {
    margin-top: 0;
    border-width: 1px;
    border-style: solid;
    border-color: #7a56ff;
    background: #7a56ff;
    color: #fff;
    padding: 0 30px;
    text-transform: uppercase;
    font-weight: 500;
    line-height: 42px;
    font-size: 14px;
    letter-spacing: 0;
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -ms-border-radius: 0;
    -o-border-radius: 0;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    display: inline-block;
    text-align: center;
    position: relative;
}